
import {
  defineComponent,
  ref,
  reactive,
  computed,
  watch,
  onBeforeUpdate,
  nextTick,
  onMounted,
} from "vue";

interface pageOption {
  value: number;
  text: number | string;
}

interface tableSetting {
  isSlotMode: boolean;
  isCheckAll: boolean;
  isHidePaging: boolean;
  keyColumn: string;
  page: number;
  pageSize: number;
  maxPage: number;
  offset: number;
  limit: number;
  paging: Array<number>;
  order: string;
  sort: string;
  pageOptions: Array<pageOption>;
}

interface column {
  isKey: string;
  field: string;
}

export default defineComponent({
  name: "my-table",
  emits: [
    "return-checked-rows",
    "do-search",
    "is-finished",
    "get-now-page",
    "row-clicked",
  ],
  props: {
    // ÊòØÂê¶ËÆÄÂèñ‰∏≠ (is data loading)
    isLoading: {
      type: Boolean,
      require: true,
    },
    // ÊòØÂê¶Âü∑Ë°å‰∫ÜÈáçÊñ∞Êü•Ë©¢ (Whether to perform a re-query)
    isReSearch: {
      type: Boolean,
      require: true,
    },
    // ÊúâÁÑ°Checkbox (Presence of Checkbox)
    hasCheckbox: {
      type: Boolean,
      default: false,
    },
    // CheckboxÂãæÈÅ∏ÂæåËøîÂõûË≥áÊñôÁöÑÂûãÊÖã (Returns data type for checked of Checkbox)
    checkedReturnType: {
      type: String,
      default: "key",
    },
    // Ê®ôÈ°å (title)
    title: {
      type: String,
      default: "",
    },
    // ÊòØÂê¶ÈéñÂÆöÁ¨¨‰∏ÄÊ¨Ñ‰Ωç‰ΩçÁΩÆ (Fixed first column's position)
    isFixedFirstColumn: {
      type: Boolean,
      default: false,
    },
    // Ê¨Ñ‰Ωç (Field)
    columns: {
      type: Array,
      default: () => {
        return [];
      },
    },
    // Ë≥áÊñô (data)
    rows: {
      type: Array,
      default: () => {
        return [];
      },
    },
    // Ë≥áÊñôÂàóÈ°ûÂà• (data row classes)
    rowClasses: {
      type: [Array, Function],
      default: () => {
        return [];
      },
    },
    // ‰∏ÄÈ†ÅÈ°ØÁ§∫Á≠ÜÊï∏ (Display the number of items on one page)
    pageSize: {
      type: Number,
      default: 10,
    },
    // Á∏ΩÁ≠ÜÊï∏ (Total number of transactions)
    total: {
      type: Number,
      default: 100,
    },
    // ÁèæÂú®È†ÅÊï∏ (Current page number)
    page: {
      type: Number,
      default: 1,
    },
    // ÊéíÂ∫èÊ¢ù‰ª∂ (Sort condition)
    sortable: {
      type: Object,
      default: () => {
        return {
          order: "id",
          sort: "asc",
        };
      },
    },
    // È°ØÁ§∫ÊñáÂ≠ó (Display text)
    messages: {
      type: Object,
      default: () => {
        return {
          pagingInfo: "Showing {0}-{1} of {2}",
          pageSizeChangeLabel: "Row count:",
          gotoPageLabel: "Go to page:",
          noDataAvailable: "No data",
        };
      },
    },
    // ÈùúÊÖãÊ®°Âºè (Static mode(no refresh server data))
    isStaticMode: {
      type: Boolean,
      default: false,
    },
    // ÊèíÊßΩÊ®°Âºè (V-slot mode)
    isSlotMode: {
      type: Boolean,
      default: false,
    },
    // ÊòØÂê¶Èö±ËóèÊèõÈ†ÅË≥áË®ä (Hide paging)
    isHidePaging: {
      type: Boolean,
      default: false,
    },
    // ‰∏ÄÈ†ÅÈ°ØÁ§∫Á≠ÜÊï∏‰∏ãÊãâÈÅ∏ÂñÆ (Dropdown of Display the number of items on one page)
    pageOptions: {
      type: Array,
      default: () => [
        {
          value: 10,
          text: 10,
        },
        {
          value: 25,
          text: 25,
        },
        {
          value: 50,
          text: 50,
        },
      ],
    },
    // ÂàÜÈ°ûÊ¢ù‰ª∂ (Key of grouping)
    groupingKey: {
      type: String,
      default: "",
    },
    // ÊòØÂê¶È°ØÁ§∫Èö±ËóèÂàÜÈ°ûÈñãÈóú (Has hide group rows toggle)
    hasGroupToggle: {
      type: Boolean,
      default: false,
    },
    // ÂÆ¢Ë£ΩÂåñÂàÜÈ°ûÈ°ØÁ§∫ (Customize grouping title)
    groupingDisplay: {
      type: Function,
      default: null,
    },
  },
  setup(props, { emit, slots }) {
    let localTable = ref<HTMLElement | null>(null);

    // Ê™¢Êü•‰∏ãÊãâÈÅ∏ÂñÆ‰∏≠ÊòØÂê¶ÂåÖÂê´È†êË®≠‰∏ÄÈ†ÅÈ°ØÁ§∫Á≠ÜÊï∏ (Validate dropdown's values have page-size value or not)
    let tmpPageOptions = props.pageOptions as Array<pageOption>;
    let defaultPageSize =
      props.pageOptions.length > 0 ? ref(tmpPageOptions[0].value) : ref(props.pageSize);
    if (tmpPageOptions.length > 0) {
      tmpPageOptions.forEach((v: pageOption) => {
        if (
          Object.prototype.hasOwnProperty.call(v, "value") &&
          Object.prototype.hasOwnProperty.call(v, "text") &&
          props.pageSize == v.value
        ) {
          defaultPageSize.value = v.value;
        }
      });
    }

    // ÁµÑ‰ª∂Áî®ÂÖßÈÉ®Ë®≠ÂÆöÂÄº (Internal set value for components)
    const setting: tableSetting = reactive({
      // ÊòØÂê¶ÂïüÁî®SlotÊ®°Âºè (Enable slot mode)
      isSlotMode: props.isSlotMode,
      // ÊòØÂê¶ÂÖ®ÈÅ∏ (Whether to select all)
      isCheckAll: false,
      // ÊòØÂê¶Èö±ËóèÊèõÈ†ÅË≥áË®ä (Hide paging)
      isHidePaging: props.isHidePaging,
      // KEYÊ¨Ñ‰ΩçÂêçÁ®± (KEY field name)
      keyColumn: computed(() => {
        let key = "";
        Object.assign(props.columns).forEach((col: column) => {
          if (col.isKey) {
            key = col.field;
          }
        });
        return key;
      }),
      // Áï∂ÂâçÈ†ÅÊï∏ (current page number)
      page: props.page,
      // ÊØèÈ†ÅÈ°ØÁ§∫Á≠ÜÊï∏ (Display count per page)
      pageSize: defaultPageSize.value,
      // ÊúÄÂ§ßÈ†ÅÊï∏ (Maximum number of pages)
      maxPage: computed(() => {
        if (props.total <= 0) {
          return 0;
        }
        let maxPage = Math.floor(props.total / setting.pageSize);
        let mod = props.total % setting.pageSize;
        if (mod > 0) {
          maxPage++;
        }
        return maxPage;
      }),
      // Ë©≤È†ÅÊï∏Ëµ∑ÂßãÂÄº (The starting value of the page number)
      offset: computed(() => {
        return (setting.page - 1) * setting.pageSize + 1;
      }),
      // Ë©≤È†ÅÊï∏ÊúÄÂ§ßÂÄº (Maximum number of pages0
      limit: computed(() => {
        let limit = setting.page * setting.pageSize;
        return props.total >= limit ? limit : props.total;
      }),
      // ÊèõÈ†ÅÈô£Âàó (Paging array)
      paging: computed(() => {
        let startPage = setting.page - 2 <= 0 ? 1 : setting.page - 2;
        if (setting.maxPage - setting.page <= 2) {
          startPage = setting.maxPage - 4;
        }
        startPage = startPage <= 0 ? 1 : startPage;
        let pages = [];
        for (let i = startPage; i <= setting.maxPage; i++) {
          if (pages.length < 5) {
            pages.push(i);
          }
        }
        return pages;
      }),
      // ÁµÑ‰ª∂ÂÖßÁî®ÊéíÂ∫è (Sortable for local)
      order: props.sortable.order,
      sort: props.sortable.sort,
      pageOptions: computed(() => {
        const ops: pageOption[] = [];
        props.pageOptions?.forEach((o) => {
          ops.push({
            value: (o as pageOption).value,
            text: (o as pageOption).text,
          });
        });
        return ops;
      }),
    });

    // ÁµÑ‰ª∂ÂÖßÁî®Ë≥áÊñô (Data rows for local)
    const localRows = computed(() => {
      // sort rows
      let rows = props.rows as Array<any>;
      // refs https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Intl/Collator/compare
      var collator = new Intl.Collator(undefined, {
        numeric: true,
        sensitivity: "base",
      });
      let sortOrder = setting.sort === "desc" ? -1 : 1;
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      rows.sort((a: any, b: any): number => {
        return collator.compare(a[setting.order], b[setting.order]) * sortOrder;
      });

      let result = null;
      if (props.groupingKey) {
        // If have set grouping-key create group temp data
        let tmp = {} as any;
        rows.forEach((v: any) => {
          if (!tmp[v[props.groupingKey]]) {
            tmp[v[props.groupingKey]] = [];
          }
          tmp[v[props.groupingKey]].push(v);
        });

        result = {} as any;
        for (let index = setting.offset - 1; index < setting.limit; index++) {
          result[rows[index][props.groupingKey]] = tmp[rows[index][props.groupingKey]];
        }
      } else {
        result = [];
        for (let index = setting.offset - 1; index < setting.limit; index++) {
          result.push(rows[index]);
        }
      }

      nextTick(function () {
        // Ë≥áÊñôÂÆåÊàêÊ∏≤ÊüìÂæåÂõûÂÇ≥ÁßÅÊúâÂÖÉ‰ª∂
        callIsFinished();
      });

      return result;
    });

    ////////////////////////////
    //
    //  Checkbox Áõ∏ÈóúÊìç‰Ωú
    //  (Checkbox related operations)
    //

    // ÂÆöÁæ©CheckboxÂèÉÁÖß (Define Checkbox reference)
    const rowCheckbox = ref([]);
    if (props.hasCheckbox) {
      /**
       * ÈáçÊñ∞Ê∏≤ÊüìÂâçÂü∑Ë°å (Execute before re-rendering)
       */
      onBeforeUpdate(() => {
        // ÊØèÊ¨°Êõ¥Êñ∞ÂâçÈÉΩÊääÂÄºÂÖ®ÈÉ®Ê∏ÖÁ©∫ (Clear all values before each update)
        rowCheckbox.value = [];
      });

      /**
       * Áõ£ËÅΩÂÖ®ÂãæÈÅ∏Checkbox (Check all checkboxes for monitoring)
       */
      watch(
        () => setting.isCheckAll,
        (state: boolean) => {
          let isChecked: Array<string | unknown> = [];
          rowCheckbox.value.forEach((val: HTMLInputElement, i: number) => {
            if (val) {
              val.checked = state;
              if (val.checked) {
                if (props.checkedReturnType == "row") {
                  isChecked.push(localRows.value[i]);
                } else {
                  isChecked.push(val.value);
                }
              }
            }
          });
          // ÂõûÂÇ≥Áï´Èù¢‰∏äÈÅ∏‰∏äÁöÑË≥áÊñô (Return the selected data on the screen)
          emit("return-checked-rows", isChecked);
        }
      );
    }

    /**
     * CheckboxÈªûÊìä‰∫ã‰ª∂ (Checkbox click event)
     */
    const checked = (event: Event) => {
      event.stopPropagation();
      let isChecked: Array<string | unknown> = [];
      rowCheckbox.value.forEach((val: HTMLInputElement, i: number) => {
        if (val && val.checked) {
          if (props.checkedReturnType == "row") {
            isChecked.push(localRows.value[i]);
          } else {
            isChecked.push(val.value);
          }
        }
      });
      // ÂõûÂÇ≥Áï´Èù¢‰∏äÈÅ∏‰∏äÁöÑË≥áÊñô (Return the selected data on the screen)
      emit("return-checked-rows", isChecked);
    };

    /**
     * Ê∏ÖÁ©∫Áï´Èù¢‰∏äÊâÄÊúâÈÅ∏ÊìáË≥áÊñô (Clear all selected data on the screen)
     */
    const clearChecked = () => {
      rowCheckbox.value.forEach((val: HTMLInputElement) => {
        if (val && val.checked) {
          val.checked = false;
        }
      });
      // ÂõûÂÇ≥Áï´Èù¢‰∏äÈÅ∏‰∏äÁöÑË≥áÊñô (Return the selected data on the screen)
      emit("return-checked-rows", []);
    };

    ////////////////////////////
    //
    //  ÊéíÂ∫è¬∑ÊèõÈ†ÅÁ≠â Áõ∏ÈóúÊìç‰Ωú
    //  (Sorting, page change, etc. related operations)
    //

    /**
     * ÂëºÂè´Âü∑Ë°åÊéíÂ∫è (Call execution sequencing)
     */
    const doSort = (order: string) => {
      let sort = "asc";
      if (order == setting.order) {
        // ÊéíÂ∫è‰∏≠ÁöÑÈ†ÖÁõÆÊôÇ (When sorting items)
        if (setting.sort == "asc") {
          sort = "desc";
        }
      }
      let offset = (setting.page - 1) * setting.pageSize;
      let limit = setting.pageSize;
      setting.order = order;
      setting.sort = sort;
      emit("do-search", offset, limit, order, sort);

      // Ê∏ÖÁ©∫Áï´Èù¢‰∏äÈÅ∏ÊìáÁöÑË≥áÊñô (Clear the selected data on the screen)
      if (setting.isCheckAll) {
        // ÂèñÊ∂àÂÖ®ÈÅ∏ÊôÇËá™ÁÑ∂ÊúÉÊ∏ÖÁ©∫ (It will be cleared when you cancel all selections)
        setting.isCheckAll = false;
      } else {
        if (props.hasCheckbox) {
          clearChecked();
        }
      }
    };

    /**
     * ÂàáÊèõÈ†ÅÁ¢º (Switch page number)
     *
     * @param page      number  Êñ∞È†ÅÁ¢º    (New page number)
     * @param prevPage  number  ÁèæÂú®È†ÅÁ¢º  (Current page number)
     */
    const changePage = (page: number, prevPage: number) => {
      setting.isCheckAll = false;
      let order = setting.order;
      let sort = setting.sort;
      let offset = (page - 1) * setting.pageSize;
      let limit = setting.pageSize;
      if (!props.isReSearch || page > 1 || page == prevPage) {
        // ÈùûÈáçÊñ∞Êü•Ë©¢ÁôºÁîüÁöÑÈ†ÅÁ¢ºËÆäÂãïÊâçÂü∑Ë°åÂëºÂè´Êü•Ë©¢ (Call query will only be executed if the page number is changed without re-query)
        emit("do-search", offset, limit, order, sort);
      }
    };
    // Áõ£ËÅΩÈ†ÅÁ¢ºÂàáÊèõ (Monitor page switching)
    watch(() => setting.page, changePage);
    // Áõ£ËÅΩÊâãÂãïÈ†ÅÁ¢ºÂàáÊèõ (Monitor manual page switching)
    watch(
      () => props.page,
      (val) => {
        if (val <= 1) {
          setting.page = 1;
          emit("get-now-page", setting.page);
        } else if (val >= setting.maxPage) {
          setting.page = setting.maxPage;
          emit("get-now-page", setting.page);
        } else {
          setting.page = val;
        }
      }
    );

    /**
     * ÂàáÊèõÈ°ØÁ§∫Á≠ÜÊï∏ (Switch display number)
     */
    const changePageSize = () => {
      if (setting.page === 1) {
        // Ê≤íËá™ÂãïËß∏Áôº changePage()ÔºåÊâÄ‰ª•ÊâãÂãïËß∏Áôº
        changePage(setting.page, setting.page);
      } else {
        // Âº∑Âà∂ËøîÂõûÁ¨¨‰∏ÄÈ†Å,‰∏¶Ëá™ÂãïËß∏Áôº changePage()
        setting.page = 1;
        setting.isCheckAll = false;
      }
    };
    // Áõ£ËÅΩÁµÑ‰ª∂ÂÖßÈ°ØÁ§∫Á≠ÜÊï∏ÂàáÊèõ (Monitor display number switch from component)
    watch(() => setting.pageSize, changePageSize);
    // Áõ£ËÅΩ‰æÜËá™PropÁöÑÈ°ØÁ§∫Á≠ÜÊï∏ÂàáÊèõ (Monitor display number switch from prop)
    watch(
      () => props.pageSize,
      (newPageSize) => {
        setting.pageSize = newPageSize;
      }
    );

    /**
     * ‰∏ä‰∏ÄÈ†Å (Previous page)
     */
    const prevPage = () => {
      if (setting.page == 1) {
        // Â¶ÇÊûúÊòØÁ¨¨‰∏ÄÈ†ÅÔºå‰∏ç‰∫àÂü∑Ë°å (If it is the first page, it will not be executed)
        return false;
      }
      setting.page--;
    };

    /**
     * ÁßªÂãïËá≥ÊåáÂÆöÈ†ÅÊï∏ (Move to the specified number of pages)
     */
    const movePage = (page: number) => {
      setting.page = page;
    };

    /**
     * ‰∏ã‰∏ÄÈ†Å (Next page)
     */
    const nextPage = () => {
      if (setting.page >= setting.maxPage) {
        // Â¶ÇÊûúÁ≠âÊñºÂ§ßÊñºÊúÄÂ§ßÈ†ÅÊï∏Ôºå‰∏çËàáÂü∑Ë°å (If it is equal to or greater than the maximum number of pages, no execution)
        return false;
      }
      setting.page++;
    };

    // Áõ£ËÅΩË≥áÊñôËÆäÊõ¥ (Monitoring data changes)
    watch(
      () => props.rows,
      () => {
        if (props.isReSearch || props.isStaticMode) {
          setting.page = 1;
        }
        nextTick(function () {
          // Ë≥áÊñôÂÆåÊàêÊ∏≤ÊüìÂæåÂõûÂÇ≥ÁßÅÊúâÂÖÉ‰ª∂ (Return the private components after the data is rendered)
          if (!props.isStaticMode) {
            callIsFinished();
          }
        });
      }
    );

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const stringFormat = (template: string, ...args: any[]) => {
      return template.replace(/{(\d+)}/g, function (match, number) {
        return typeof args[number] != "undefined" ? args[number] : match;
      });
    };

    // Call „Äåis-finished„Äç Method
    const callIsFinished = () => {
      if (localTable.value) {
        let localElement = localTable.value.getElementsByClassName("is-rows-el");
        emit("is-finished", localElement);
      }
      emit("get-now-page", setting.page);
    };

    // Data rows for grouping (Default-mode only)
    const groupingRows = computed(() => {
      let result = {} as any;
      props.rows.forEach((v: any) => {
        if (!result[v[props.groupingKey]]) {
          result[v[props.groupingKey]] = [];
        }
        result[v[props.groupingKey]].push(v);
      });

      nextTick(function () {
        callIsFinished();
      });

      return result;
    });

    /**
     * Toggle Group rows
     *
     * @param {Event}  e
     * @param {string} trId
     */
    const toggleGroup = (e: Event, trId: string) => {
      let element = e.target as HTMLElement;
      element?.parentElement?.classList.toggle("rotated-90");
      document.getElementsByName("vtl-group-" + trId).forEach((element) => {
        element.classList.toggle("hidden");
      });
    };

    /**
     * ÁµÑ‰ª∂ÊéõËºâÂæå‰∫ã‰ª∂ (Mounted Event)
     */
    onMounted(() => {
      nextTick(() => {
        if (props.rows.length > 0) {
          callIsFinished();
        }
      });
    });

    if (props.hasCheckbox) {
      // ÈúÄË¶Å Checkbox ÊôÇ (When Checkbox is needed)
      return {
        slots,
        localTable,
        localRows,
        setting,
        rowCheckbox,
        checked,
        doSort,
        prevPage,
        movePage,
        nextPage,
        stringFormat,
        groupingRows,
        toggleGroup,
      };
    } else {
      return {
        slots,
        localTable,
        localRows,
        setting,
        doSort,
        prevPage,
        movePage,
        nextPage,
        stringFormat,
        groupingRows,
        toggleGroup,
      };
    }
  },
  watch: {
    pageSize() {
      this.setting.pageSize = this.pageSize;
    },
  },
});
